<template lang="html">
  <div class="npassword-wrapper">
    <div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
      <headers></headers>
    </div>
    <el-row type="flex" :gutter="20" justify="space-between">
      <el-col :span="4">
        <div class="grid-content bg-purple">
          <ul>
            <router-link tag="li" active-class="on" :to="{path:'/indexz/indexStaff'}">
              员工管理<i class="el-icon-arrow-right"></i>
            </router-link>
            <router-link tag="li" active-class="on" :to="{path:'/indexz/indexRole'}">
              角色管理<i class="el-icon-arrow-right"></i>
            </router-link>
            <router-link tag="li" active-class="on" :to="{path:'/indexz/indexnpassword'}">
              修改密码<i class="el-icon-arrow-right"></i>
            </router-link>
          </ul>
        </div>
      </el-col>
      <el-col :span="20">
        <div class="grid-content bg-purple-light">
          <router-view></router-view>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  }
}
</script>

<style lang="less">
.npassword-wrapper{
  min-width: 1200px;
}
.el-row {
  background: #F5F6FB;
}
.bg-purple {
  background: white;
  border: 1px solid #e6e9f2;
  border-top: 0;
  height: 100%;
  ul{
    background: white;
    li{
      text-align: center;
      line-height: 60px;
      border-bottom: 1px solid #e6e9f2;
      font-size: 13px;
      font-weight: bold;
      color: #999;
      &.on{
       color: #2e51a5;
       font-weight: bolder;
      }
      .el-icon-arrow-right{
        font-size: 12px;
        color: #ccc;
        position: relative;
        left: 30px;
      }
    }
  }

}
.bg-purple-light {
  min-height: 665px;
}
.grid-content {
  // min-height: 540px;
}
</style>



